<template>
  <div>
    <div>
      <b-card id="filterCard" no-body>
        <b-row>
          <b-col class="mb-2 mr-sm-2 mb-sm-0 title-fw">
            <label class="label mb-2 mr-sm-2 mb-sm-0">
              {{ $t("FILTER") }}
            </label>
          </b-col>
        </b-row>
        <b-row>
          <b-col class="mb-6" cols="12" xl="3">
            <b-input-group>
              <b-input-group-prepend>
                <b-button size="sm" variant="info" class="append-btn">{{
                  $t("PERSONINCHARGENAME")
                }}</b-button>
              </b-input-group-prepend>
              <DropDownSearch
                @selected_person_data="selectedPerson"
                :searchList="processorNameOptions"
                class="searchSelect1"
              />
            </b-input-group>
          </b-col>
          <b-col class="mb-1 py-1" cols="12" xl="4">
            <b-form-checkbox-group
              id="checkbox-group-1"
              v-model="inputFilter.permission"
              :options="permissionOptions"
              @change="updateFilter"
              class="small-fs"
              size="sm"
            ></b-form-checkbox-group>
          </b-col>
          <b-col class="mb-1" cols="12" xl="3">
            <b-input-group class="mb-2 mr-sm-2 mb-sm-0">
              <b-input-group-prepend>
                <b-button size="sm" variant="info" class="append-btn">{{
                  $t("AFFILIATEDCOMPANY")
                }}</b-button>
              </b-input-group-prepend>
              <DropDownSearch
                @selected_person_data="personCompanyData"
                :searchList="affiliatedCompanyOptions"
                class="searchSelect1"
              />
              <span
                ><i
                  v-if="affiliatedCompanyLoading"
                  class="fa fa-spinner fa-spin"
              /></span>
            </b-input-group>
          </b-col>
          <b-col class="mb-1" cols="12" xl="2">
            <b-button
              :disabled="!getContentAccess()"
              @click="addPersonIncharge"
              variant="primary"
              class="Detailsbtn person-btn"
              >{{ $t("ADD.PERSON.INCHARGE") }}</b-button
            >
          </b-col>
        </b-row>
      </b-card>
      <b-card>
        <b-row class="table-height">
          <b-col xl="12">
            <b-table
              ref="selectableTable"
              :fields="fields"
              :items="tableData"
              :busy="isBusy"
              :bordered="false"
              :striped="false"
              :borderless="false"
              :outlined="false"
              fixed
              responsive="sm"
              sticky-header
              head-variant="light"
              small="small"
              show-empty
            >
              <template v-slot:empty>
                <div class="text-center errorColor">
                  {{ $t("GENRALDATAERRORMESSAGE") }}
                </div>
              </template>
              <template #table-colgroup="scope">
                <col
                  v-for="field in scope.fields"
                  :key="field.key"
                  :style="{
                    width: field.key === 'checkState' ? '30px' : '110px',
                  }"
                />
              </template>
              <template v-slot:cell(checkState)="item">
                <b-form-radio
                  :id="'radio-' + item.index"
                  v-model="item.item.checkState"
                  :name="'table-radio'"
                  @change="costToggleSelectAll(item)"
                ></b-form-radio>
              </template>
              <template v-slot:cell(InchargeName)="item">{{
                item.value
              }}</template>
              <template v-slot:cell(Permission)="item">{{
                item.value
              }}</template>
              <template v-slot:cell(AffiliatedCompony)="item">{{
                item.value
              }}</template>
              <template v-slot:cell(Department)="item">{{
                item.value
              }}</template>
              <template v-slot:cell(Position)="item">{{ item.value }}</template>
              <template v-slot:cell(MailAddress)="item">{{
                item.value
              }}</template>
              <template v-slot:cell(Authority)="item">{{
                item.value
              }}</template>
              <template v-slot:cell(CreatedDate)="item">{{
                item.value
              }}</template>
              <template v-slot:cell(UpdateDate)="item">{{
                item.value
              }}</template>
              <template v-slot:cell(Icon)="item">
                <b-button
                  :disabled="
                    !getContentAccess() && personID != item.item.person_id
                  "
                  @click="getPersonData(item)"
                  variant="primary"
                  class="Detailsbtn"
                  >{{ $t("EDIT") }}</b-button
                >
              </template>
              <template v-slot:table-busy>
                <div class="text-center text-danger my-2">
                  <b-spinner class="align-middle"></b-spinner>
                </div>
              </template>
            </b-table>
          </b-col>
        </b-row>
      </b-card>
    </div>
    <b-row>
      <b-col cols="12" xl="4" class="mb-1">
        <b-button
          @click="deletePersonIncharge"
          :disabled="checkedItems.length == 0"
          variant="primary"
          >{{ $t("DELETEBATCHPERSONINCHARGE") }}</b-button
        >
      </b-col>
      <b-col cols="12" xl="5" class="mb-1 mt-1">
        <b-pagination
          v-model="currentPage"
          :total-rows="totalRows"
          :per-page="limit"
          size="sm"
        >
          <template v-slot:prev-text>{{ $t("PREVIOUS") }}</template>
          <template v-slot:next-text>{{ $t("NEXT") }}</template>
          <template v-slot:page="{ page, active }">
            <strong v-if="active">{{ page }}</strong>
            <i v-else>{{ page }}</i>
          </template>
        </b-pagination>
      </b-col>
      <b-col cols="12" xl="3" class="mb-1">
        <div class="countList">
          <label>{{ $t("MEANS") }}</label
          >&nbsp;&nbsp;
          <template v-for="(page, index) in pageOptions">
            <b-link :key="index" v-if="limit == page" href="javascript:;">
              {{ page }}
              <span v-if="index != pageOptions.length - 1">&nbsp;|&nbsp;</span>
            </b-link>
            <b-link
              :key="index"
              @click="perPageChange(page)"
              v-else
              href="javascript:;"
            >
              <u>{{ page }}</u>
              <span v-if="index != pageOptions.length - 1">&nbsp;|&nbsp;</span>
            </b-link>
          </template>
        </div>
      </b-col> </b-row
    ><br />
  </div>
</template>

<script>
import DropDownSearch from "@/components/DropDownSearch";
import { ApiServicesFactory } from "@/shared/apiServiceFactory";
const BasicInfoApis = ApiServicesFactory.get("basic");
import { getDate } from "@/shared/utils";
import i18n from "@/plugins/i18n";
import { endPoint } from "@/shared/endPoint";

const defaultForm = {
  person_name: "",
  permission: [],
};
export default {
  name: "PersonInchargeList",
  components: {
    DropDownSearch,
  },
  data() {
    return {
      affiliatedCompanyOptions: [],
      total: 0,
      limit: 50,
      start: 0,
      currentPage: 1,
      pageOptions: [50, 100, 200],
      inputFilter: {},
      isBusy: false,
      company_name: "",
      affiliatedCompanyLoading: false,
      tableData: [],
      checkedItems: [],
      processorNameOptions: [],
      personID: "",
    };
  },
  computed: {
    totalRows() {
      if (this.total) {
        return this.total;
      }
      return this.getRowCount();
    },
    fields() {
      return [
        {
          key: "checkState",
          label: "",
          class: "text-left",
        },
        {
          key: "InchargeName",
          label: this.$t("PERSONINCHARGETABLE.PERSONINCHARGENAME"),
          class: "text-left",
        },
        {
          key: "Permission",
          label: this.$t("PERSONINCHARGETABLE.ACCOUNTPERMISSION"),
          class: "text-left",
        },
        {
          key: "AffiliatedCompony",
          label: this.$t("AFFILIATEDCOMPANY"),
          class: "text-left",
        },
        {
          key: "Department",
          label: this.$t("PERSONINCHARGETABLE.DEPARTMENT"),
          class: "text-left",
        },
        {
          key: "Position",
          label: this.$t("PERSONINCHARGETABLE.POSITION"),
          class: "text-left",
        },
        {
          key: "MailAddress",
          label: this.$t("PERSONINCHARGETABLE.MAILADDRESS"),
          class: "text-left",
        },
        {
          key: "Authority",
          label: this.$t("PERSONINCHARGETABLE.AUTHORITY"),
          class: "text-center",
        },
        {
          key: "CreatedDate",
          label: this.$t("PERSONINCHARGETABLE.CREATEDDATE"),
          class: "text-center",
        },
        {
          key: "UpdateDate",
          label: this.$t("PERSONINCHARGETABLE.UPDATEDATE"),
          class: "text-center",
        },
        {
          key: "Icon",
          label: "",
          class: "text-center",
        },
      ];
    },
    items() {
      return this.tableData;
    },
    permissionOptions() {
      return [
        { text: i18n.t("OPERATOR"), value: "1" },
        { text: i18n.t("SUPPLIERREPRESENTATIVE"), value: "2" },
        { text: i18n.t("SYSTEMADMINISTRATOR"), value: "3" },
      ];
    },
  },
  watch: {
    currentPage() {
      this.pageChange(this.currentPage);
    },
    startPage() {
      this.currentPage = this.startPage + 1;
    },
  },
  mounted() {
    this.getPersonInchargeList();
    this.getAffiliatedCompanyList();
    this.readPerson();
    this.personID = localStorage.getItem("personID");
  },
  created() {
    this.inputFilter = { ...defaultForm };
  },
  methods: {
    /**
     * @description function to fetch item length
     * @returns {number}
     */
    getRowCount: function () {
      return this.items.length;
    },
    /**
     * @description function to update list items on change of page
     * @param {any} page
     */
    pageChange(page) {
      this.start = page - 1;
      this.getPersonInchargeList();
    },
    /**
     * @description function to fetch item length
     * @param {any} page
     */
    perPageChange(page) {
      this.limit = page;
      this.getPersonInchargeList();
    },
    /**
     * @description function to fetch content access as per current person auth
     * @returns {boolean}
     */
    getContentAccess() {
      let person_auth = localStorage.getItem("person_auth");
      return person_auth == "02" ? true : false;
    },
    /**
     * @description function to fetch person incharge data
     * @param {any} item
     */
    personCompanyData(item) {
      this.company_name = item.value;
      this.getPersonInchargeList();
    },
    /**
     * @description function to update person incharge data
     */
    updateFilter() {
      setTimeout(() => {
        this.getPersonInchargeList();
      }, 0);
    },
    /**
     * @description function to store person data & navigate to person management add
     */
    addPersonIncharge() {
      this.$store.dispatch("updatePersonAndMode", {
        person_id: "",
        isEditmode: false,
      });
      this.$router.push({
        name: "personMgmt_addEdit",
      });
    },
    /**
     * @description function to choose and fetch person incharge data
     * @param {any} item
     */
    selectedPerson(item) {
      this.inputFilter.person_name = item.text;
      this.getPersonInchargeList();
    },
    /**
     * @description function to store person data & navigate to person management edit
     * @param {any} item
     */
    getPersonData(item) {
      //need to store create or update mode.
      this.$store.dispatch("updatePersonAndMode", {
        person_id: item.item.person_id,
        isEditmode: true,
      });

      this.$router.push({
        name: "personMgmt_addEdit",
      });
    },
    /**
     * @description function to toggle items selection in a table
     * @param {any} item
     */
    costToggleSelectAll(item) {
      this.checkedItems = [];
      let selectedRow = item.item;
      this.checkedItems.push(selectedRow);
    },
    /**
     * @description function to remove selected person data with confirmation pop-up
     */
    async deletePersonIncharge() {
      this.$swal({
        text: i18n.t("DELETEPERSONINCHARGE"),
        type: "warning",
        showCancelButton: true,
        confirmButtonText: i18n.t("YESDELETE"),
        cancelButtonText: i18n.t("NODELETE"),
      }).then(async (result) => {
        if (result.value) {
          /* create person id list to be deleted */
          let personIds = [];
          this.checkedItems.forEach((person) => {
            personIds.push(person.person_id);
          });

          let compiledArray = {
            conditions: [
              {
                key: "person_id",
                value: personIds,
              },
            ],
            update_id: localStorage.getItem("personID"),
          };
          const { data } = await BasicInfoApis.postAPI(
            endPoint.MASTERSOFTDELETEMPERSON,
            compiledArray
          );

          if (data.success) {
            this.getPersonInchargeList();
          } else {
            this.$swal({ type: "error", text: i18n.t("NOTDELETED") });
          }
        }
      });
    },

    /**
     * @description function to fetch affiliated company data
     */
    async getAffiliatedCompanyList() {
      this.affiliatedCompanyLoading = true;
      let compiledArray = {
        fieldlist: ["code", "value_string_small"],
        conditions: [
          {
            key: "m_general_purpose.key",
            value: "AFFI",
          },
        ],
      };
      const { data } = await BasicInfoApis.postAPI(
        endPoint.READMGENERALPURPOSE,
        compiledArray
      );
      this.affiliatedCompanyLoading = false;

      if (data.success) {
        let responseData = data.data;

        /* create formatted options array data for dropdown search */
        this.affiliatedCompanyOptions = [];
        if (responseData.length) {
          responseData.forEach((data) => {
            this.affiliatedCompanyOptions.push({
              value: data.code,
              text: data.value_string_small,
            });
          });
        }
      }
    },
    /**
     * @description function to fetch person incharge data for table with filter conditions
     */
    async getPersonInchargeList() {
      this.isBusy = true;

      /* where conditions */
      let conditions = [];
      if (this.inputFilter.person_name) {
        conditions.push({
          key: "m_person.person_name",
          value: this.inputFilter.person_name,
          operator: "like",
        });
      }
      if (this.company_name) {
        conditions.push({
          key: "m_person.person_company",
          value: this.company_name,
          operator: "like",
        });
      }
      if (this.inputFilter.permission.length) {
        this.inputFilter.permission.forEach((p) => {
          switch (p) {
            case "1":
              conditions.push({
                key: "apl_manager_account.wiz_auth_flg",
                value: true,
                operator: "=",
              });
              break;
            case "2":
              conditions.push({
                key: "apl_manager_account.supplier_auth_flg",
                value: true,
                operator: "=",
              });
              break;
            case "3":
              conditions.push({
                key: "apl_manager_account.system_auth_flg",
                value: true,
                operator: "=",
              });
              break;

            default:
              break;
          }
        });
      }
      /* payload with server side pagination option */
      let compiledArray = {
        conditions: conditions,
        limit: this.limit,
        offset: this.start * 10,
        paginate: true,
      };

      const { data } = await BasicInfoApis.postAPI(
        endPoint.PERSONLIST,
        compiledArray
      );
      this.isBusy = false;
      this.total = 0;

      if (data.success) {
        let responseData = data.data.data;
        let mapData = [];
        this.total = data.data.total;
        /* construction of table data format */
        responseData.forEach((item) => {
          let listData = {
            checkState: false,
            person_id: item.person_id,
            InchargeName: item.person_name,
            Permission: this.getPermission(
              item.wiz_auth_flg,
              item.supplier_auth_flg,
              item.system_auth_flg
            ),
            AffiliatedCompony: item.person_company,
            Department: item.person_department,
            Position: this.getPersonPosition(item.person_position),
            MailAddress: item.person_mail,
            Authority: this.getPersonAuth(item.person_auth_kbn),
            CreatedDate: getDate(new Date(item.create_dt)),
            UpdateDate: getDate(new Date(item.update_dt)),
          };
          mapData.push(listData);
        });
        this.tableData = mapData;
      }
    },
    /**
     * @description function to get person position name based on person position code
     * @param {any} position
     * @returns {string}
     */
    getPersonPosition(position) {
      let person_position = "";
      switch (position) {
        case "001":
          person_position = i18n.t("PERSONPOSITION.GENERAL"); //General
          break;
        case "002":
          person_position = i18n.t("PERSONPOSITION.CHIEF"); //Chief
          break;
        case "003":
          person_position = i18n.t("PERSONPOSITION.SECTIONMANAGER"); //Section Manager
          break;
        case "004":
          person_position = i18n.t("PERSONPOSITION.HOD"); //Head of the department
          break;
        case "005":
          person_position = i18n.t("PERSONPOSITION.EXECUTIVE"); //Executive or officer
          break;
        default:
          person_position = "";
          break;
      }
      return person_position;
    },
    /**
     * @description function to get person auth name based on person auth code
     * @param {any} auth
     * @returns {string}
     */
    getPersonAuth(auth) {
      let person_auth = "";
      switch (auth) {
        case "01":
          person_auth = i18n.t("PERSONPOSITION.GENERAL"); //General
          break;
        case "02":
          person_auth = i18n.t("ADMIN"); //Admin
          break;

        default:
          person_auth = "";
          break;
      }
      return person_auth;
    },
    /**
     * @description function to get permission based on flags
     * @param {boolean} wiz_auth_flg
     * @param {boolean} supplier_auth_flg
     * @param {boolean} system_auth_flg
     * @returns {string}
     */
    getPermission(wiz_auth_flg, supplier_auth_flg, system_auth_flg) {
      let permission = [];
      if (wiz_auth_flg) {
        permission.push(i18n.t("OPERATOR"));
      }
      if (supplier_auth_flg) {
        permission.push(i18n.t("SUPPLIERREPRESENTATIVE"));
      }
      if (system_auth_flg) {
        permission.push(i18n.t("SYSTEMADMINISTRATOR"));
      }
      return permission.toString();
    },
    /**
     * @description function to fetch person data
     */
    async readPerson() {
      const { data } = await BasicInfoApis.postAPI(endPoint.MASTERREADMPERSON, {
        fieldlist: ["person_id", "person_name"],
      });
      if (data.success) {
        let responseData = data.data;
        /* create formatted options array data for dropdown search */
        if (responseData.length) {
          responseData.forEach((data) => {
            this.processorNameOptions.push({
              value: data.person_id,
              text: data.person_name,
            });
          });
        }
      } else {
        this.processorNameOptions = [];
      }
    },
  },
};
</script>
<style scoped lang="scss">
.searchSelect1 {
  position: relative !important;
  flex: 1 1 auto !important;
  width: 1% !important;
  margin-bottom: 0 !important;
  height: 28px !important;
}
.input-group-text {
  background-color: none;
  border: none;
}
#filterCard {
  border: dashed 1px #000 !important;
  margin-bottom: 0.5rem;
  padding: 15px 15px 0px 15px;
}
.append-btn {
  min-width: 10px;
  border: none !important;
  background: none !important;
}
.custom-select {
  border-radius: 0px !important;
}
.form-control {
  border-radius: inherit;
  height: 35px;
}
.Detailsbtn {
  min-width: 75px;
}
#filterCard .row > .col,
.row > [class*="col-"] {
  padding-right: 5px;
  padding-left: 5px;
}
.person-btn {
  min-width: 138px !important;
}
</style>
<style lang="scss">
.custom-control-label {
  font-size: 11px !important;
  line-height: 21px !important;
}
.custom-control-inline {
  margin-right: 0.5rem !important;
}
.ui.search.selection.dropdown > input.search,
.ui.search.selection.dropdown > span.sizer {
  line-height: 1.21428571em !important;
  padding: 0.175rem 0.75rem !important;
  height: 35px;
}
.ui.selection.dropdown {
  border: 1px solid #a9a6a7;
  border-radius: 0px;
  height: 30px !important;
  padding: 0.175rem 0.75rem !important;
}
.ui.selection.dropdown:focus {
  color: #5c6873 !important;
  background-color: #fff !important;
  border-color: #0f61cb !important;
  outline: 0 !important;
  box-shadow: 0 0 0 0.2rem rgba(6, 40, 84, 0.25) !important;
}
.ui.selection.active.dropdown:hover {
  border-color: #0f61cb !important;
  box-shadow: 0 0 0 0.2rem rgba(6, 40, 84, 0.25) !important;
}
.ui.search.dropdown > .text {
  padding-top: 7px;
}
</style>